import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  FormEvent,
} from "react";
import { IGoal, Color, colors } from "data/goal";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { addGoal } from "store/goals/actions";
import { useFormFields } from "hooks/useFormFields";
import Avatar from "react-avatar";
import Moment from "react-moment";
import "moment-timezone";

const InitialState: IGoal = {
  id: 1,
  title: "",
  color: "#81F5FF",
  type: "Hard-skill",
  assignedTo: [],
  achievedBy: [],
  activityIds: [],
  createdAt: new Date(),
  updatedAt: new Date(),
  achievedAt: null,
};

interface IProps {
  setNewMode: any;
  setSelected: any;
}

export const SideView: React.FunctionComponent<IProps> = (props) => {
  const { setNewMode, setSelected } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields(InitialState);

  const tabs = ["Goal", "Colleagues"];
  const [tabMode, setTabMode] = useState(0);
  const [selectedColor, setSelectedColor] = useState(
    InitialState.color as string
  );

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(addGoal({ ...fields, color: selectedColor }));
    setNewMode(false);
    setSelected(fields);
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="sideview-functions">
        <div className="item"></div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          <div className="action">
            <input type="submit" value="add" />
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <Avatar
              name={fields.title}
              size="100"
              maxInitials={2}
              round={"20px"}
              className="avatar"
              color={selectedColor}
              textSizeRatio={2.4}
            />
            <input
              id="title"
              placeholder="Add title..."
              value={fields.title}
              onChange={handleFieldChange}
            />
          </div>
          <div className="sideview-content-item col">
            <label htmlFor="goal-color">Goal Color</label>
            <div id="goal-color">
              {colors.map((item, index) => (
                <div
                  key={index}
                  className={
                    item === selectedColor
                      ? "color-item selected"
                      : "color-item"
                  }
                  style={{
                    backgroundColor: item,
                  }}
                  onClick={() => setSelectedColor(item)}
                ></div>
              ))}
            </div>
          </div>
          <div className="sideview-content-item col">
            <label htmlFor="type">Type</label>
            <select id="type" value={fields.type} onChange={handleFieldChange}>
              <option value="Hard-skill">Hard-skill</option>
              <option value="Soft-skill">Soft-skill</option>
            </select>
          </div>
        </div>
      </div>
    </form>
  );
};

export default SideView;
