import React, { useState, useEffect, useRef, useCallback } from "react";
import { IGoal, Color, colors } from "data/goal";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { editGoal } from "store/goals/actions";
import { removeGoal } from "store/goals/actions";
import { useFormFields } from "hooks/useFormFields";
import Avatar from "react-avatar";
import Moment from "react-moment";
import "moment-timezone";

interface IProps {
  selected: IGoal;
  setSelected: any;
  setDeleting: any;
}

export const SideView: React.FunctionComponent<IProps> = (props) => {
  const {
    id,
    title,
    color,
    type,
    assignedTo,
    achievedBy,
    createdAt,
    updatedAt,
    achievedAt,
  } = props.selected;
  const { selected, setSelected, setDeleting } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields(selected);
  const [editMode, setEditMode] = useState(false);
  const [editIcon, deleteIcon] = sideviewIcons;
  const [lastEdit, setLastEdit] = useState(updatedAt);

  const tabs = ["Goal", "Colleagues"];
  const [tabMode, setTabMode] = useState(0);
  const [selectedColor, setSelectedColor] = useState(color as string);

  useEffect(() => {
    setEditMode(false);
    // setEditorBody(body);
    setSelectedColor(color);
    setForm(selected);
  }, [selected]);

  useEffect(() => {
    if (editMode) {
      let timer = setTimeout(() => {
        setLastEdit(new Date());
        dispatch(
          editGoal({ ...fields, color: selectedColor, updatedAt: lastEdit })
        );
      }, 1000);
      return () => {
        clearTimeout(timer);
        setSelected(selected);
      };
    }
  }, [fields, selectedColor]);

  const deleteItem = () => {
    setDeleting(true);
    dispatch(removeGoal(id));
  };

  return (
    <div>
      <div className="sideview-functions">
        <div className="item">
          Edited <Moment fromNow>{lastEdit}</Moment>
        </div>
        <div
          className={editMode ? "item edit active" : "item edit"}
          onClick={() => setEditMode(!editMode)}
        >
          {editIcon}
        </div>
        <div className="item delete" onClick={deleteItem}>
          {deleteIcon}
        </div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <Avatar
              name={fields.title}
              size="100"
              maxInitials={2}
              round={"20px"}
              className="avatar"
              color={selectedColor}
              textSizeRatio={2.4}
            />
            <input
              id="title"
              placeholder="Add title..."
              value={fields.title}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item col">
            <label htmlFor="goal-color">Goal Color</label>
            <div id="goal-color">
              {colors.map((item, index) => (
                <div
                  key={index}
                  className={
                    item === selectedColor
                      ? "color-item selected"
                      : "color-item"
                  }
                  style={{
                    backgroundColor: item,
                  }}
                  onClick={() => editMode ? setSelectedColor(item) : null}
                ></div>
              ))}
            </div>
          </div>
          <div className="sideview-content-item col">
            <label htmlFor="type">Type</label>
            <select
              id="type"
              className={editMode ? undefined : "hide-icon"}
              value={fields.type}
              onChange={handleFieldChange}
              disabled={!editMode}
            >
              <option value="Hard-skill">Hard-skill</option>
              <option value="Soft-skill">Soft-skill</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SideView;
