import { useState, useEffect } from "react";

export function usePrimaryColor() {
  const [color, setColor] = useState<string>(
    localStorage.getItem("primaryColor") as string
  );

  useEffect(() => {
    let currentColor = getComputedStyle(
      document.documentElement
    ).getPropertyValue("--primary");

    if (!color) {
      localStorage.setItem("primaryColor", currentColor);
      setColor(currentColor);
      document.documentElement.style.setProperty("--primary", currentColor);
    } else if (color !== currentColor) {
      document.documentElement.style.setProperty("--primary", color);
    }
  }, [color]);

  // function changeColor(newColor: string) {
  //   localStorage.setItem("primaryColor", newColor);
  //   setColor(newColor);
  //   document.documentElement.style.setProperty("--primary", newColor);

  //   return newColor;
  // }

  const changeColor = (newColor: string) => {
    localStorage.setItem("primaryColor", newColor);
    setColor(newColor);
    document.documentElement.style.setProperty("--primary", newColor);
  };

  return [color, setColor, changeColor] as const;
}

