import { IPureAction } from "store/types";
import { ActionTypes } from "data/actionTypes";
import { IGoal, IGoalState } from "data/goal";
import API from "store/api";
import { store } from "_helpers";

// Fetch request to API
export const fetchGoalsRequest = async () => {
  try {
    const response = await API.get(`posts`);
    return store.dispatch(fetchSuccess(response.data));
  } catch (error) {
    console.log(error);
    return store.dispatch(fetchError(error));
  }
};

// Set activities
export const fetchSuccess = (
  data: IGoalState
): IPureAction<ActionTypes.GET_GOALS_SUCCESS, IGoalState> => ({
  type: ActionTypes.GET_GOALS_SUCCESS,
  payload: data,
});

// Error if fetch has failed
export const fetchError = (
  error: Error
): IPureAction<ActionTypes.GET_GOALS_FAILURE, Error> => ({
  type: ActionTypes.GET_GOALS_FAILURE,
  payload: error,
});

// Add new goal
export const addGoal = (
  data: IGoal
): IPureAction<ActionTypes.ADD_GOAL, IGoal> => ({
  type: ActionTypes.ADD_GOAL,
  payload: data,
});

// Edit goal
export const editGoal = (
  data: IGoal
): IPureAction<ActionTypes.EDIT_GOAL, IGoal> => ({
  type: ActionTypes.EDIT_GOAL,
  payload: data,
});

// Remove Goal
export const removeGoal = (
  id: number
): IPureAction<ActionTypes.REMOVE_GOAL, Number> => ({
  type: ActionTypes.REMOVE_GOAL,
  payload: id,
});

// Remove Goal
export const getGoalById = (
  id: number
): IPureAction<ActionTypes.GET_GOAL_BY_ID, Number> => ({
  type: ActionTypes.GET_GOAL_BY_ID,
  payload: id,
});
