import React, { useState, useEffect } from "react";
import { RouteComponentProps, Link } from "react-router-dom";
import { useTypedSelector } from "store/_reducers";

import { routes } from "data/routes";
import { useTheme } from "hooks/useTheme";
import { usePrimaryColor } from "hooks/usePrimaryColor";
import { colors } from "data/goal"

export const Profile: React.FunctionComponent<RouteComponentProps> = () => {
  const profile = useTypedSelector(state => state.authenticationReducer.user);
  const [theme, toggleTheme] = useTheme();
  const { email, name } = profile;

  const [color, setColor, changeColor] = usePrimaryColor();

  return (
    <section className="profile-page">
      <label>
        Dark Mode:
          <input
          name="isGoing"
          type="checkbox"
          checked={theme}
          onChange={toggleTheme} />
      </label>
      <div>{email}</div>
      {/* {colors.map(item => {
        <div onClick={() => changeColor(item)}></div>
      })} */}
      <input type="text" value={color} onChange={e => changeColor(e.target.value)} />
      <div>{name}</div>
      <div>password</div>
      <div>repeat password</div>
      <Link to={routes.Login} className="logout"><i>Log uit</i></Link>
    </section>
  );
};

export default Profile;
