import React, { useState } from "react";
import { IActivity, IActivityState } from "data/activity";
import { listIcons } from "data/featherIcons";

interface IProps {
  dividerState: any;
  toggleDivider: any;
}

export const Divider: React.FunctionComponent<IProps> = (props) => {
  const { dividerState, toggleDivider } = props;
  // const [status, setStatus] = useState(false)
  const [arrowLeftIcon, arrowRightIcon] = listIcons;

  return (
    <div className="divider" onClick={toggleDivider}>
      {dividerState ? arrowRightIcon : arrowLeftIcon}
    </div>
  );
};

export default Divider;
