import React, { useEffect, useState, FormEvent } from "react";

import { login, logout } from "store/users/actions";
import { RouteComponentProps } from "react-router-dom";
import { useFormFields } from "hooks/useFormFields";
import Input from "components/UI/Input";
import { loginIcons } from "data/featherIcons"

export const Login: React.FunctionComponent<RouteComponentProps> = props => {
  const [mailIcon, keyIcon] = loginIcons;
  const [fields, handleFieldChange] = useFormFields({
    email: "",
    password: "",
  });
  const [submitted, setSubmitted] = useState(false);

  useEffect(() => {
    logout();
  }, []);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setSubmitted(true);
    if (fields) {
      login(fields, props.history);
    }
  };

  return (
    <div className="login-wrapper">
      <form name="form" className="login-form" onSubmit={handleSubmit}>
        <div className="login-text">
          <div className="header-text">Supervise<b>me</b></div>
          <div className="sub-text">Welcome back!</div>
        </div>
        <div className="login-input">
          <Input id="email" name="email" icon={mailIcon} className="form-control" placeholder="Enter your email" value={fields.email} onChange={handleFieldChange} required />
          <Input type="password" id="password" name="password" icon={keyIcon} className="form-control" placeholder="Enter your password" value={fields.password} onChange={handleFieldChange} required />
        </div>
        <div className="login-button">
          <button className="btn btn-primary">Login</button>
        </div>
      </form>
    </div>
  );
};

export default Login;
