import { useState, useEffect } from "react";

export function useDivider() {
  const [dividerState, setDividerState] = useState<Boolean>(
    JSON.parse(localStorage.getItem("dividerState") as any)
  );

  const toggleDivider = () => {
    localStorage.setItem("dividerState", String(!dividerState));
    setDividerState(!dividerState);
  };

  return [dividerState, toggleDivider];
}
