import React, { useEffect, useState } from "react";
import { IGoal, IGoalState } from "data/goal";
import { IUser, IUserState } from "data/user";
import { IActivity, IActivityState } from "data/activity";
import { ReactSortable } from "react-sortablejs";
import Avatar from "react-avatar";
import Activities from "components/Activities";

type DataTypes = IGoalState | IUserState | IActivityState;
type DataType = IGoal | IUser | IActivity;

interface IProps {
  listItems: any[];
  goals?: IGoal[];
  searchTerm: string;
  setSearchResults: any;
  selected: any;
  setSelected: any;
  setNewMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const {
    listItems,
    goals,
    selected,
    setSelected,
    setNewMode,
    setSearchResults,
  } = props;

  const selectItem = (item: IGoal | IUser | IActivity) => {
    setSelected(item);
    setNewMode(false);
  };

  return (
    <div className="list">
      {/* ACTIVITY */}
      {listItems
        ? listItems.map((item) => (
          <div
            className={
              item.id === selected.id ? "list-item selected" : "list-item"
            }
            onClick={() => selectItem(item)}
          >
            <Avatar
              name={item.title ? item.title : item.name}
              size="55"
              maxInitials={2}
              round={"20"}
              className="list-item-icon"
              textSizeRatio={2.4}
              color={item.color}
            />
            <div className="list-item-data">
              <div className="list-item-title">
                {item.title ? item.title : item.name}
              </div>
              <div className="list-item-read-time">5 minutes read</div>
            </div>
          </div>
        )) : null}
    </div>
  );
};

export default List;
