import React, { useState, useEffect, FormEvent } from "react";
import { IActivity } from "data/activity";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { getUsers, register, editUser, deleteUser } from "store/users/actions";
import { useFormFields } from "hooks/useFormFields";
import { IUser } from "data/user";
import Avatar from "react-avatar";

const InitialState: IUser = {
  id: 1,
  name: "",
  email: "",
  password: "",
  job: "Back-end developer",
  location: "Groningen",
  role: "Admin",
  updatedAt: new Date()
};

interface IProps {
  setNewMode: any;
  setSelected: any;
}

export const SideView: React.FunctionComponent<IProps> = (props) => {
  const { setNewMode, setSelected } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields(InitialState);
  const [submitted, setSubmitted] = useState(false);

  const tabs = ["Colleague", "Coached by"];
  const [tabMode, setTabMode] = useState(0);

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(
      await register(fields).then(setNewMode(false), setSelected(fields))
    );
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="sideview-functions">
        <div className="item"></div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          <div className="action">
            <input type="submit" value="add" />
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <Avatar
              name={fields.name}
              size="100"
              maxInitials={2}
              round={"20px"}
              className="avatar"
              textSizeRatio={2.4}
            />
            <input
              id="name"
              placeholder="Name..."
              value={fields.name}
              onChange={handleFieldChange}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="email">Email</label>
            <input
              id="email"
              placeholder="Email..."
              value={fields.email}
              onChange={handleFieldChange}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="password">Password</label>
            <input
              id="password"
              type="password"
              placeholder="Password..."
              value={fields.password}
              onChange={handleFieldChange}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="job">Job</label>
            <select id="job" value={fields.job} onChange={handleFieldChange}>
              <option value="Front-end developer">Front-end developer</option>
              <option value="Back-end developer">Back-end developer</option>
              <option value="Sales">Sales</option>
            </select>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="location">Location</label>
            <select
              id="location"
              value={fields.location}
              onChange={handleFieldChange}
            >
              <option value="Leeuwarden">Leeuwarden</option>
              <option value="Groningen">Groningen</option>
            </select>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="role">Role</label>
            <select id="role" value={fields.role} onChange={handleFieldChange}>
              <option value="User">User</option>
              <option value="Supervisor">Supervisor</option>
              <option value="Admin">Admin</option>
            </select>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Coached By</label>
            <div id="coached-by">
              <div className="tag">Henk Holtrop</div>
              <div className="tag">Lennart Alserda</div>
            </div>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Assigned to</label>
            <div id="coached-by">
              <div className="tag">React.Js</div>
              <div className="tag">Jira</div>
              <div className="tag">Alserda</div>
            </div>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Completed</label>
            <div id="coached-by">
              <div className="tag">Drupal</div>
              <div className="tag">Angular</div>
            </div>
          </div>
        </div>
      </div>
    </form>
  );
};

export default SideView;
