import createReducer, { ReducerHandler } from "_helpers/createReducer";
import { IGoal } from "data/goal";
import { ActionTypes } from "data/actionTypes";
import * as mutators from "../goals/mutations";

export interface IGoalsState {
  goals: IGoal[];
  error?: Error;
}
const initialState: IGoalsState = {
  goals: [
    {
      id: 1,
      title: "React.Js",
      color: "#AACFCF",
      type: "Hard-skill",
      assignedTo: [],
      achievedBy: [],
      activityIds: [484937, 484938, 484939, 484940, 484941, 484942],
      createdAt: new Date(),
      updatedAt: new Date(),
      achievedAt: null,
    },
    {
      id: 2,
      title: "Welkom Stagiair",
      color: "#FFB6B6",
      type: "Soft-skill",
      assignedTo: [],
      achievedBy: [],
      activityIds: [484943, 484944, 484945, 484946, 484947],
      createdAt: new Date(),
      updatedAt: new Date(),
      achievedAt: null,
    },
  ],
  error: undefined,
};

const handlers: ReducerHandler<IGoalsState> = {
  [ActionTypes.GET_GOALS_SUCCESS]: mutators.getSuccess,
  [ActionTypes.GET_GOALS_FAILURE]: mutators.getFailure,
  [ActionTypes.ADD_GOAL]: mutators.addGoal,
  [ActionTypes.EDIT_GOAL]: mutators.editGoal,
  [ActionTypes.REMOVE_GOAL]: mutators.removeGoal,
  [ActionTypes.GET_GOAL_BY_ID]: mutators.getGoalById,
};

export const goalReducer = createReducer<IGoalsState>(initialState, handlers);
