import React from "react";

type ButtonTheme = (
    'blue' |
    'red' |
    'green'
)

interface IProps {
    // theme?: ButtonTheme;
    // ietsCustoms?: string;
    icon?: any;
    extra?: any;
}

type Props = IProps & React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

// type Props = React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

// const Input: React.FC<Props> = ({ theme, ietsCustoms, ...rest }) => {

const Input: React.FC<Props> = ({ ...rest }) => {

    return (
        <div className="input-wrapper">
            <i className="input-search-icon">{rest.icon}</i>
            <input {...rest} />
            <i className="input-search-extra">{rest.extra}</i>
        </div>
    )
}

export default Input;